import React, { Component } from 'react';


import SearchPanel from './../search-panel';
import Filter from './../filter';
import TodoList from './../todo-list';
import Task from '../task';

import classes from'./app.module.scss';

export default class App extends Component{

  state = {
    todoList: [
      {id: 1, text: 'Wake up', filter: 'done'},
      {id: 2, text: 'Eat breakfast', filter: 'done'},
      {id: 3, text: 'Go to work', filter: 'important'}
    ]
  }
  
  delItem = (id) => {
    this.setState(({todoList}) => {
      
      const idx = todoList.findIndex((item) => id === item.id);
      const newTodoList = [...todoList.slice(0, idx), ...todoList.slice(idx + 1)];

      return{
        todoList: newTodoList
      }

    })
  }

  render() {
    return (
      <div className = {classes.appBlock}>
          <SearchPanel/>
          <Filter/>
          <TodoList 
              todos = {this.state.todoList}
              onDelItem = {this.delItem}/>
          <Task/>
      </div>
    );
  }
  
}